import React, { useState } from 'react';
import { isMobile } from 'react-device-detect';
import Paper from '@material-ui/core/Paper';
import { Grid, IconButton, makeStyles } from '@material-ui/core';
import Container from '../components/container';
import Popover from '@material-ui/core/Popover';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import NavigationBar from '../components/navigation';
import SocialButton from '../components/socialButton';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import EcoIcon from '@material-ui/icons/Eco';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import fem from '../assets/iconos/fem.png';
import pel from '../assets/iconos/pel.png';
import eco from '../assets/iconos/eco.png';
import { Grow } from '@material-ui/core';
import FingerprintIcon from '@material-ui/icons/Fingerprint';

export default function Home() {
	const styles = useStyles();

	return (
		<Container>
			<div style={{ position: 'absolute', top: isMobile ? 30 : 0 }}>
				<SocialButton />
			</div>

			<div className={styles.descJor}>
				<DescJor />
				<IconButton />
			</div>

			<div
				style={{
					position: 'absolute',
					bottom: 20,
					alignSelf: 'center',
				}}
			>
				<NavigationBar />
			</div>
		</Container>
	);
}

const ejesCamp = [
	{ icon: <FingerprintIcon />, name: 'info' },
	{ icon: <EcoIcon />, name: 'ambiente' },
	{ icon: <SportsSoccerIcon />, name: 'deportes' },
	{
		icon: (
			<Avatar styles={{ height: 28, width: 28 }} alt='feminismo' src={fem} />
		),
		name: 'genero',
	},
];

const DescJor = () => {
	const styles = useStyles();
	const [ejespop, setEjespop] = useState(false);
	const [anchorEl, setAnchorEl] = useState(null);
	const [ejeType, setEjeType] = useState('info');

	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};
	const open = Boolean(anchorEl);
	const id = open ? 'simple-popover' : undefined;

	function switchText() {
		let text;
		switch (ejeType) {
			case 'deporte':
				text =
					'"Eje  Deportes, efwekfmwekl fmwelkfm welkgfmw lekgmnwekl gnmwek ngwle gnwejgnwe;gmweklcfmweklm fnvwejl gnwe;kljfm wekl;fn mwegn wejklgnwejgn ;wjngwej ng"';
				break;
			case 'genero':
				text =
					'"Eje Genero, efwekfmwekl fmwelkfm welkgfmw lekgmnwekl gnmwek ngwle gnwejgnwe;gmweklcfmweklm fnvwejl gnwe;kljfm wekl;fn mwegn wejklgnwejgn ;wjngwej ng"';
				break;
			case 'ambiente':
				text =
					'"Eje Ambiente, efwekfmwekl fmwelkfm welkgfmw lekgmnwekl gnmwek ngwle gnwejgnwe;gmweklcfmweklm fnvwejl gnwe;kljfm wekl;fn mwegn wejklgnwejgn ;wjngwej ng"';
				break;
			default:
				text =
					'"Soy Jorgelina, nací en Santa Fe. Soy Vicepresidenta Segunda del	Concejo Deliberante de Nuestra Ciudad. Mi compromiso es trabajar todos	los días para contribuir a que seamos una ciudad pujante, inclusiva y con igualdad de oportunidades para todos los santafesinos y santafesinas"';
				break;
		}

		return text;
	}

	return (
		<>
			<Paper className={styles.paperDesc}>
				<TextEffect active={ejeType} />
			</Paper>

			<div className={styles.ejesButton}>
				{}
				<Fab
					aria-label='info'
					size='medium'
					onClick={() => {
						setEjeType('info');
					}}
				>
					<FingerprintIcon />
				</Fab>
				<Fab
					aria-label='medio ambiente'
					size='medium'
					onClick={() => {
						setEjeType('ambiente');
					}}
				>
					<EcoIcon />
				</Fab>
				<Fab
					aria-label='deportes'
					size='medium'
					onClick={() => setEjeType('deportes')}
				>
					<SportsSoccerIcon />
				</Fab>
				<Fab
					aria-label='genero'
					size='medium'
					onClick={() => setEjeType('genero')}
				>
					<Avatar className={styles.iconSize} alt='feminismo' src={fem} />
				</Fab>
			</div>
		</>
	);
};

const texts = {
	info: '"Soy Jorgelina, nací en Santa Fe. Soy Vicepresidenta Segunda del	Concejo Deliberante de Nuestra Ciudad. Mi compromiso es trabajar todos	los días para contribuir a que seamos una ciudad pujante, inclusiva y con igualdad de oportunidades para todos los santafesinos y santafesinas"',
	deportes:
		'"Eje  Deportes, efwekfmwekl fmwelkfm welkgfmw lekgmnwekl gnmwek ngwle gnwejgnwe;gmweklcfmweklm fnvwejl gnwe;kljfm wekl;fn mwegn wejklgnwejgn ;wjngwej ng"',
	genero:
		'"Eje  Deportes, efwekfmwekl fmwelkfm welkgfmw lekgmnwekl gnmwek ngwle gnwejgnwe;gmweklcfmweklm fnvwejl gnwe;kljfm wekl;fn mwegn wejklgnwejgn ;wjngwej ng"',
	ambiente:
		'"Eje  Deportes, efwekfmwekl fmwelkfm welkgfmw lekgmnwekl gnmwek ngwle gnwejgnwe;gmweklcfmweklm fnvwejl gnwe;kljfm wekl;fn mwegn wejklgnwejgn ;wjngwej ng"',
};

const TextEffect = ({ active }) => {
	return (
		<div>
			{Object.keys(texts).map((key) => {
				if (key === active) {
					return (
						<Grow in={true} timeout={500}>
							<Typography>{texts[key]}</Typography>
						</Grow>
					);
				}
			})}
		</div>
	);
};

const useStyles = makeStyles((theme) => ({
	typography: {
		padding: theme.spacing(2),
	},
	iconSize: { height: 28, width: 28 },
	descJor: {
		//marginTop: isMobile ? '85%' : '15%',
		marginBottom: isMobile ? 60 : '15%',
		width: isMobile ? '95%' : '35%',
		maxWidth: 480,
		marginLeft: isMobile ? 'auto' : '9%',
		marginRight: isMobile ? 'auto' : null,
	},
	paperDesc: {
		minHeight: isMobile ? null : 220,
		maxHeight: isMobile ? 200 : 350,
		overflow: 'auto',
		padding: isMobile ? 15 : 35,
		lineHeight: isMobile ? 1.2 : 1.6,
		letterSpacing: isMobile ? 1 : 2.2,
		fontSize: isMobile ? 12 : 18,
		borderRadius: 10,
		backgroundColor: 'rgba(228, 235, 241,0.7)',
		textAlign: 'justify',
	},
	ejesButton: {
		marginTop: isMobile ? -18 : -25,
		width: isMobile ? '55%' : '45%',
		marginRight: 'auto',
		marginLeft: 'auto',
		display: 'flex',
		justifyContent: 'space-between',
	},
}));
